import common from '../../../common/common.js'

export default {
	data() {
		return {
			key:'',
			shopInfo:[],		//用户信息
			goodsList:[],
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		
		that.getGoodsList();

	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		
		that.getGoodsList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		//搜索商品
		inputkeys(e){
			let that = this
			let key = e.detail.value
			that.key = key
			that.getGoodsList();
		},
		//删除商品
		delGoods(e){
			var that = this
			var goodsid = e.currentTarget.dataset.goodsid
			var index = e.currentTarget.dataset.index
			uni.showModal({
				title: '提示',
				content: '确定要删除吗？',
				success: res => {
					if(res.confirm){
						uni.request({
							url: common.sjApiUrl + 'merchant/delgoods',
							method: 'GET',
							data: {
								goodsid:goodsid
							},
							success: res => {
								var data = res.data
								if(data.code == 0){
									uni.showToast({
										title: '操作成功',
										mask: false,
										duration: 1500
									});
									that.goodsList.splice(index,1);
								}
							},
							fail: () => {},
							complete: () => {}
						});
					}
				},
			});
		},
		//上下架
		optionGoods(e){
			var that = this
			var goodsid = e.currentTarget.dataset.goodsid
			var status = e.currentTarget.dataset.status
			var index = e.currentTarget.dataset.index
			uni.request({
				url: common.sjApiUrl + 'merchant/optionGoods',
				method: 'GET',
				data: {
					goodsid:goodsid,
					status:status
				},
				success: res => {
					console.log(res)
					var data = res.data
					var n_status = status == 1 ? 0 : 1;

					that.goodsList[index].goods_status = n_status
					uni.showToast({
						title:'操作成功',
						duration:1000,
						icon:'success'
					})
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//商品列表
		getGoodsList(){
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			//console.log(userInfo);
			uni.request({
				url: common.sjApiUrl + 'merchant/getGoodsList',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id,
					key:that.key
				},
				success: res => {						
					that.goodsList = res.data.goods_list						
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url: url
			});
		},
		
	}
}
